import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useHistory, useLocation } from "react-router";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import { toast } from "react-toastify";
export default function PaymentApprove() {
  const [data, setData] = useState([]);
  const [fileName, setFileName] = useState("");
  const location = useLocation();
  const history = useHistory();

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/ViewSingleInvoice",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { id: location.state.id },
      };

      const invoiceApiResp = await trackPromise(axios(config));
      // console.log(
      //   "invoiceApiResp - " + JSON.stringify(invoiceApiResp.data.msg)
      // );
      //console.log(invoiceApiResp.data.msg.file_path);
      setFileName(extractNameFromPath(invoiceApiResp.data.msg.file_path));
      setData(invoiceApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  /*PAYMENT STATUS START*/
  function paymentStatus(id, status) {
    //console.log("ID: " + id + "Status: " + status);

    postData(id, status);
    async function postData(id, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/UpdateInvoiceApprovalStatus",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          id: id,
          status: "Approved",
        },
      };
      const paymentStatusApiResp = await trackPromise(axios(config));
      //console.log("paymentStatusApiResp - " + paymentStatusApiResp.data);
      if (paymentStatusApiResp.data.status === "ok") {
        toast("Payment Approved Successfully", {
          className: "toast-error",
        });

        history.push("/payment-list-view");
      } else {
        toast(paymentStatusApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }

    // post data to admin module end
  }
  /*PAYMENT STATUS END*/

  function extractNameFromPath(file_path) {
    //console.log(typeof file_path);
    var index = file_path.indexOf("_");
    var result = file_path.substr(index + 1);
    return result;
  }
  function downloadFile(file_path) {
    //console.log("file_path: " + file_path);
    window.open(file_path);
  }
  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Payment Information</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/payment-list-view"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <table className="table table-sm table-borderless table-responsive table-font-100">
                            <tbody className="p-0 m-0">
                              <tr>
                                <td className="pl-0 text-left">Invoice No</td>
                                <td className="pl-0 pr-0 text-left">
                                  : {data.id}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0 text-left">Institute ID</td>
                                <td className="pl-0 pr-0 text-left">
                                  : {data.institute_id}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0 text-left">
                                  Institute Name
                                </td>
                                <td className="pl-0 pr-0 text-left">
                                  : {data.institute_name}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0 text-left">
                                  Billing Month
                                </td>
                                <td className="pl-0 pr-0 text-left">
                                  : {data.month}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0 text-left">Billing Year</td>
                                <td className="pl-0 pr-0 text-left">
                                  : {data.year}
                                </td>
                              </tr>
                              <tr className="bottom-border">
                                <td className="pl-0 text-left">
                                  Payment Document
                                </td>
                                <td
                                  className="pl-0 pr-0 text-left"
                                  title="Download File"
                                  style={{ cursor: "pointer" }}
                                  onClick={() => downloadFile(data.file_path)}
                                >
                                  :{" "}
                                  <span className="link_style">{fileName}</span>
                                </td>
                              </tr>
                            </tbody>
                          </table>
                          {data.status === "Approved" ? null : (
                            <div className="d-flex float-left mb-3">
                              <button
                                type="submit"
                                className="btn btn-update mt-2 mb-2"
                                onClick={() =>
                                  paymentStatus(data.id, data.status)
                                }
                              >
                                Approve
                              </button>
                            </div>
                          )}

                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
