import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useLocation } from "react-router";
import { useHistory, useParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function PackageEdit() {
  const { register, handleSubmit, errors } = useForm();
  const location = useLocation();
  let parameter = [];
  let packages = {};
  const history = useHistory();
  //console.log(location);

  //get package parameter from setting -start
  const [parameterSetting, setParameterSetting] = useState([]);
  //get package parameter from setting -end
  //GET PACKAGE DATA -START  url: "/package-detail",
  const [data, setData] = useState({});
  const [parameterDB, setParameterDB] = useState([]);
  const [selectedOption, setSelectedOption] = useState("");
  //GET PACKAGE DATA -END
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      //get package parameter from setting -start-----------------------------------
      var config = {
        method: "get",
        url: "/GetSettingsPackageList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      let response = await trackPromise(axios(config));
      //console.log("response Setting - " + JSON.stringify(response.data.data));
      let apiResponse = response.data.data;
      const apiResponseFiltered = apiResponse.filter(
        (res) => res.active === "1"
      );
      //console.log(JSON.stringify(apiResponseFiltered));
      //setParameterSetting(apiResponseFiltered);
      //get package parameter from setting -end-----------------------------------------

      //GET PACKAGE DATA -START  url: "/package-detail",*******************************
      var config = {
        method: "post",
        url: "/package-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { package_id: location.state.id },
      };

      const pkgApiResp = await trackPromise(axios(config));
      //console.log("pkgApiRespDB - " + JSON.stringify(pkgApiResp.data.msg));
      setData(pkgApiResp.data.msg);
      //console.log("pkgApiResp - " + JSON.stringify(pkgApiResp.data.msg.params));
      setParameterDB(pkgApiResp.data.msg.params);
      setSelectedOption(pkgApiResp.data.msg.display); // set value for radio button display
      //GET PACKAGE DATA -END****************************************************************

      let paramSetting = apiResponseFiltered;
      let paramDB = pkgApiResp.data.msg.params;

      //console.log("executing....");
      // -----------------------------------------------
      let matchFlag = false;
      for (var i = 0, len = paramDB.length; i < len; i++) {
        for (var j = 0, len2 = paramSetting.length; j < len2; j++) {
          //console.log("Parameter: " + j + "- " + paramDB[j].name);
          if (paramDB[i].name === paramSetting[j].package_item_name) {
            //console.log("Matched Parameter: " + paramDB[j].name);
            paramSetting[j]["quantity"] = paramDB[i].quantity;
            paramSetting[j]["price"] = paramDB[i].price;
            matchFlag = true;
          }
        }
        if (!matchFlag) {
          //console.log("Create: " + paramSetting[i].package_item_name);
          paramSetting.push({
            package_item_name: paramDB[i].name,
            quantity: paramDB[i].quantity,
            price: paramDB[i].price,
            package_number: 1,
            package_amount: parseFloat(paramDB[i].price / paramDB[i].quantity),
          });
        }
      }
      //console.log(JSON.stringify(paramSetting));
      setParameterSetting(paramSetting);
    }
    fetchData();
  }, []);

  //get parameter array of object format
  function getPackageParameter(data) {
    //console.log("DATA: " + JSON.stringify(data));
    var total_parameter = 0;
    for (let key in data) {
      if (data.hasOwnProperty(key)) {
        if (key.includes("name-")) {
          total_parameter = total_parameter + 1;
        }
      }
    }

    for (let i = 0; i < total_parameter; i++) {
      let object = {};
      let name_key = `name-${i}`;
      // let quantity_key = data.quantity[i];
      // console.log("quantity_key: " + data[quantity_key]);
      let price_key = `price-${i}`;

      object["name"] = data[name_key];
      //console.log(`quantity-${i}` + data.quantity[i]);
      object["quantity"] = data.quantity[i];
      object["price"] = data[price_key];

      parameter.push(object);
    }
    return parameter;
    //console.log(JSON.stringify(parameter));
  }
  // UPDATE PACKAGE DATA -START url: "/package-update",
  const onSubmit = (data) => {
    //seperate package vlaues from data object start
    packages = {};
    const parameters = getPackageParameter(data);
    //console.log("parameters: " + JSON.stringify(parameters));
    //console.log("parameters.length: " + parameters.length);
    if (checkMinimumParameterError(parameters)) {
      setMinimumParameterError(true);
    } else {
      setMinimumParameterError(false);
      const packageData = {
        id: location.state.id,
        title: data.packageTitle,
        description: data.packageDescription,
        display: data.display,
        type: "default",
        payable_amount: data.payableAmount,
        monthly_service_charge: data.monservcharge,
        total_amount: data.totalAmount,
        discounted_amount: data.discountAmount,
        parameters: parameters,
      };
      //console.log("Package Data: " + JSON.stringify(packageData));
      //seperate package vlaues from data object end

      updatePkgData(packages);
      async function updatePkgData(packages) {
        let accessToken = localStorage.getItem("token");
        //alert(accessToken);
        accessToken = accessToken.substring(1, accessToken.length - 1);
        var config = {
          method: "post",
          url: "/package-update",
          headers: {
            Authorization: `Bearer ${accessToken}`,
            "Content-Type": "application/json",
          },
          data: packageData,
        };
        //console.log("PACKAGE DATA---" + JSON.stringify(packageData));
        const updatePkgApiResp = await trackPromise(axios(config));
        if (updatePkgApiResp.data.status === "ok") {
          toast("Package Updated Successfully", {
            className: "toast-error",
          });
          history.push("/package-list-view");
        } else {
          toast(updatePkgApiResp.data.msg, {
            className: "toast-error",
          });
        }
      }
    }
  };
  // UPDATE PACKAGE DATA -END

  function checkMinimumParameterError(parameter) {
    //console.log(parameter);
    for (var i = 0; i < parameter.length; i++) {
      if (parameter[i].quantity !== "0") {
        //console.log(parameter[i].quantity);
        return false;
      }
    }
    return true;
  }

  const [discountError, setDiscountError] = useState(false);
  const [minimumParameterError, setMinimumParameterError] = useState(false);

  // handle input change
  const handleInputChange = (e, index, package_amount, package_number) => {
    const { name, value } = e.target;
    // console.log(
    //   "Name: " +
    //     name +
    //     "\nValue: " +
    //     value +
    //     "\nIndex: " +
    //     index +
    //     "\npackage_amount: " +
    //     package_amount +
    //     "\npackage_number: " +
    //     package_number
    // );

    //console.log("type of value: " + typeof value);
    if (parseInt(value) >= 0) {
      let quantity = 0;
      if (value) {
        quantity = parseInt(value);
      }

      let price = parseFloat(package_amount);
      let number = parseInt(package_number);

      let unitPrice = parseFloat((price / number) * quantity);
      unitPrice = parseFloat(unitPrice.toFixed(2));
      document.getElementById("price-" + index).value = unitPrice;

      calculateTotalAmount();

      calculatePayableAmount();
    } else {
    }
  };

  // calculate total amount
  function calculateTotalAmount() {
    let total_parameter = parameterSetting.length;
    let monservcharge = 0.0;

    let total = 0.0;
    for (let i = 0; i < total_parameter; i++) {
      if (document.getElementById("price-" + i).value) {
        //console.log("Price: " + document.getElementById("price-" + i).value);
        total =
          parseFloat(total) +
          parseFloat(document.getElementById("price-" + i).value);
        //console.log("Total: " + total);
      }
    }

    if (parseInt(document.getElementById("monservcharge").value) >= 0) {
      // console.log(
      //   "monservcharge: " + document.getElementById("monservcharge").value
      // );
      monservcharge = parseFloat(
        document.getElementById("monservcharge").value
      );
    } else {
      monservcharge = 0.0;
    }

    document.getElementById("totalAmount").value = parseFloat(
      total + monservcharge
    );
    return parseFloat(total + monservcharge);
  }

  // calculate payable amount = totalamount - discountamount
  function calculatePayableAmount() {
    let totalAmount = calculateTotalAmount();

    if (document.getElementById("discountAmount").value) {
      // console.log(
      //   "discountAmount: " + document.getElementById("discountAmount").value
      // );
      let discountAmount = parseFloat(
        document.getElementById("discountAmount").value
      );
      if (discountAmount >= 0) {
        if (discountAmount <= totalAmount) {
          setDiscountError(false);
          // console.log(
          //   "calculatePayableAmount: " + parseFloat(totalAmount - discountAmount)
          // );

          document.getElementById("payableAmount").value = parseFloat(
            totalAmount - discountAmount
          );
        } else {
          setDiscountError(true);
        }
      }
    } else {
      document.getElementById("payableAmount").value = 0;
    }
    return;
  }

  // radio button for Is Package Display on Frontend
  function onValueChangeRadio(event) {
    //console.log("value: " + event.target.value);
    setSelectedOption(event.target.value);
  }
  return (
    <React.Fragment>
      <div className="wrapper">
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Update Package</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/package-list-view"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            {/* package name */}
                            <div className="form-group row">
                              <div className="col">
                                <label>Package Name</label>
                                <br />
                                <input
                                  type="text"
                                  readOnly
                                  className="form-control"
                                  name="packageTitle"
                                  defaultValue={data.title}
                                  ref={register({
                                    required: "* This Field is Required",
                                    pattern: {
                                      value: /^[a-zA-Z ]+$/i,
                                      message: "* Please Enter String",
                                    },
                                  })}
                                />
                                {errors.packageTitle && (
                                  <ErrorMsg>
                                    {errors.packageTitle.message}
                                  </ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* package description */}
                            <div className="form-group row">
                              <div className="col">
                                <label>Package Description</label>
                                <br />
                                <textarea
                                  className="form-control"
                                  name="packageDescription"
                                  defaultValue={
                                    data.description ? data.description : null
                                  }
                                  ref={register()}
                                  rows="1"
                                  cols="30"
                                ></textarea>
                              </div>
                            </div>
                            {/* Is Package Display on Front-end ? */}
                            <div className="form-group row">
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>
                                  Is Package Display on Front-end ?
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div className="p-2">
                                    <input
                                      name="display"
                                      type="radio"
                                      className="mr-2"
                                      value="On"
                                      checked={selectedOption === "On"}
                                      onChange={(e) => onValueChangeRadio(e)}
                                      ref={register({ required: true })}
                                    />
                                    On
                                  </div>
                                  <div className="p-2">
                                    <input
                                      name="display"
                                      className="mr-2"
                                      type="radio"
                                      value="Off"
                                      checked={selectedOption === "Off"}
                                      onChange={(e) => onValueChangeRadio(e)}
                                      ref={register({ required: true })}
                                    />
                                    Off
                                  </div>
                                </div>

                                {errors.display && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* package parameter */}
                            <div className="form-group">
                              <label>
                                <span className="red-star">*</span> Package
                                Parameter
                              </label>
                              <br />
                              {parameterSetting.map((data, i) => {
                                return (
                                  <div className="row" key={i}>
                                    {/* Title */}
                                    <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                      <label>Title</label>
                                      <br />
                                      <input
                                        type="text"
                                        readOnly
                                        className="form-control mb-3"
                                        id={`name-${i}`}
                                        name={`name-${i}`}
                                        defaultValue={data.package_item_name}
                                        ref={register({ required: true })}
                                      />
                                    </div>
                                    {/* Quantity */}
                                    <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                      <label>Quantity</label>
                                      <br />
                                      <input
                                        type="text"
                                        className="form-control mb-3"
                                        name={`quantity[${i}]`}
                                        defaultValue={
                                          data.quantity ? data.quantity : 0
                                        }
                                        onChange={(e) =>
                                          handleInputChange(
                                            e,
                                            i,
                                            data.package_amount,
                                            data.package_number
                                          )
                                        }
                                        ref={register({
                                          required: "* This Field is Required",
                                          pattern: {
                                            value: /^[+]?\d+([.]\d+)?$/i, //only positive number 0-9
                                            message:
                                              "Please enter positive number",
                                          },
                                        })}
                                      />
                                      {errors.quantity?.[i]?.message && (
                                        <ErrorMsg>
                                          {errors.quantity?.[i]?.message}
                                        </ErrorMsg>
                                      )}
                                    </div>
                                    {/* Amount */}
                                    <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                      <label>Amount</label>
                                      <br />
                                      <input
                                        type="text"
                                        id={`price-${i}`}
                                        readOnly
                                        className="form-control mb-3"
                                        name={`price-${i}`}
                                        defaultValue={
                                          data.price
                                            ? parseFloat(data.price)
                                            : null
                                        }
                                        ref={register()}
                                      />
                                    </div>
                                  </div>
                                );
                              })}

                              {minimumParameterError ? (
                                <ErrorMsg>
                                  Minimum 1 package parameter required!
                                </ErrorMsg>
                              ) : null}
                            </div>
                            {/* Monthly Service Charge */}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                <label>Monthly Service Charge</label>
                                <br />
                                <input
                                  type="text"
                                  id="monservcharge"
                                  name="monservcharge"
                                  defaultValue={data.monthly_service_charge}
                                  onChange={() => calculatePayableAmount()}
                                  className="form-control"
                                  ref={register({
                                    pattern: {
                                      value: /^[+]?\d+([.]\d+)?$/i, //only positive number 0-9
                                      message: "Please enter positive number",
                                    },
                                  })}
                                />
                                {errors.monservcharge && (
                                  <ErrorMsg>
                                    {errors.monservcharge.message}
                                  </ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* Total amount */}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                <label>Total Amount</label>
                                <br />
                                <input
                                  type="text"
                                  readOnly
                                  id="totalAmount"
                                  name="totalAmount"
                                  defaultValue={data.total_amount}
                                  className="form-control"
                                  ref={register()}
                                />
                              </div>
                            </div>
                            {/* Discount amount */}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                <label>
                                  <span className="red-star">*</span> Discount
                                  Amount
                                </label>
                                <br />
                                {parseFloat(data.discounted_amount) >= 0 ? (
                                  <input
                                    type="text"
                                    id="discountAmount"
                                    name="discountAmount"
                                    defaultValue={
                                      parseFloat(data.discounted_amount) > 0
                                        ? parseFloat(data.discounted_amount)
                                        : parseFloat(0)
                                    }
                                    onChange={() => calculatePayableAmount()}
                                    className="form-control"
                                    ref={register({
                                      required: "* This Field is Required",
                                      pattern: {
                                        value: /^[+]?\d+([.]\d+)?$/i, //only positive number 0-9
                                        message: "Please enter positive number",
                                      },
                                    })}
                                  />
                                ) : null}

                                {discountError ? (
                                  <ErrorMsg>
                                    * Discount amount should be less than
                                    payable amount
                                  </ErrorMsg>
                                ) : null}
                                {errors.discountAmount && (
                                  <ErrorMsg>
                                    {errors.discountAmount.message}
                                  </ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* payable amount */}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                <label>Payable Amount</label>
                                <br />
                                <input
                                  type="text"
                                  readOnly
                                  id="payableAmount"
                                  name="payableAmount"
                                  defaultValue={data.payable_amount}
                                  className="form-control"
                                  ref={register()}
                                />
                              </div>
                            </div>

                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Update
                            </button>
                          </form>
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
