import React from "react";

export default function Datatable({ data, packageDetail }) {
  // console.log("Params: " + JSON.stringify(data));
  const dataLength = data.length;

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center td_slno_size">SL No.</th>
            <th className="inst-list-th text-center td_pkg_title_size">
              Title
            </th>
            <th className="inst-list-th text-center">Quantity</th>
            <th className="inst-list-th text-center">Amount</th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-left td_pkg_title_size">
                {row["name"]}
              </td>
              <td className="inst-list-td text-center">{row["quantity"]}</td>
              <td className="inst-list-td text-center">
                {row["price"] ? row["price"] : "0"}
              </td>
            </tr>
          ))}
          <tr>
            <td className="inst-list-td text-center">{dataLength + 1}</td>
            <td className="inst-list-td text-left td_pkg_title_size">
              Monthly Service Charge
            </td>
            <td className="inst-list-td text-center"></td>
            <td className="inst-list-td text-center">
              {packageDetail.monthly_service_charge}
            </td>
          </tr>
          <tr>
            <td className="inst-list-td text-center">{dataLength + 2}</td>
            <td className="inst-list-td text-left td_pkg_title_size">
              Total Amount
            </td>
            <td className="inst-list-td text-center"></td>
            <td className="inst-list-td text-center">
              <b>{packageDetail.total_amount}</b>
            </td>
          </tr>
          <tr>
            <td className="inst-list-td text-center">{dataLength + 3}</td>
            <td className="inst-list-td text-left td_pkg_title_size">
              Discount Amount
            </td>
            <td className="inst-list-td text-center"></td>
            <td className="inst-list-td text-center">
              {packageDetail.discounted_amount}
            </td>
          </tr>
          <tr>
            <td className="inst-list-td text-center">{dataLength + 4}</td>
            <td className="inst-list-td text-left td_pkg_title_size">
              Payable Amount
            </td>
            <td className="inst-list-td text-center"></td>
            <td className="inst-list-td text-center">
              <b>{packageDetail.payable_amount}</b>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
