import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function Compose() {
  const { register, handleSubmit, errors } = useForm();

  let history = useHistory();
  const emailAddress = localStorage.getItem("emailAddress");
  // POST PACKAGE DATA -START
  const onSubmit = async (data) => {
    let formData = new FormData();
    formData.append("to_address", data.to_address);
    formData.append("from_address", emailAddress);
    formData.append("title", data.title);
    formData.append("body", data.body);
    formData.append("status", "sent");
    formData.append("file_attachment", data.file_attachment[0]);
    formData.append("image_attachment", data.image_attachment[0]);

    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    var config = {
      method: "post",
      url: "/SendNewEmail",
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
      data: formData,
    };
    //console.log("DATA---" + formData);
    const response = await trackPromise(axios(config));
    //console.log("response - " + response.data);
    if (response.data.status === "ok") {
      toast("Message Sent", {
        className: "toast-error",
      });
      // history.push("/email");
      history.go(0);
    } else {
      toast(response.data.msg, {
        className: "toast-error",
      });
    }
  };
  // POST PACKAGE DATA -END

  // save as draft start
  const saveAsDraft = async (data) => {
    //console.log(data);
    let formData = new FormData();
    formData.append("to_address", data.to_address);
    formData.append("from_address", emailAddress);
    formData.append("title", data.title);
    formData.append("body", data.body);
    formData.append("status", "draft");
    formData.append("file_attachment", data.file_attachment[0]);
    formData.append("image_attachment", data.image_attachment[0]);
    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    var config = {
      method: "post",
      url: "/SaveEmailAsDraft",
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
      data: formData,
    };
    //console.log("DATA---" + formData);
    const response = await trackPromise(axios(config));
    //console.log("response - " + response.data);
    if (response.data.status === "ok") {
      toast("Message save as draft", {
        className: "toast-error",
      });
      // history.push("/email");
      history.go(0);
    } else {
      toast(response.data.msg, {
        className: "toast-error",
      });
    }
  };
  // save as draft end
  // ---------------------file and image attachment start------------------------
  const [attachment, setAttachment] = useState("");
  const [fileLink, setFileLink] = useState("");
  const [image, setImage] = useState("");
  const [imageLink, setImageLink] = useState("");

  let objectURLFile;
  let objectURLImage;
  const onchangeFileHandler = (e) => {
    //console.log(e.target.files[0]);
    setAttachment(e.target.files[0].name);
    if (objectURLFile) {
      // revoke the old object url to avoid using more memory than needed
      URL.revokeObjectURL(objectURLFile);
    }

    const file = e.target.files[0];
    objectURLFile = URL.createObjectURL(file);
    setFileLink(objectURLFile);
  };

  const onchangeImageHandler = (e) => {
    //console.log(e.target.files[0]);
    setImage(e.target.files[0].name);
    if (objectURLImage) {
      // revoke the old object url to avoid using more memory than needed
      URL.revokeObjectURL(objectURLImage);
    }

    const file = e.target.files[0];
    objectURLImage = URL.createObjectURL(file);
    setImageLink(objectURLImage);
  };

  function downloadFile(file_path) {
    //console.log("file_path: " + file_path);
    window.open(file_path);
  }
  // ---------------------file and image attachment end------------------------
  return (
    <div>
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        {/* To */}
        <div className="form-group row pb-0 mb-0">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6 pb-0 mb-0">
            <div className="d-flex">
              <label style={{ paddingTop: "8px", minWidth: "80px" }}>To</label>
              <span style={{ paddingTop: "8px" }}>:</span>
              <input
                type="text"
                className="form-control mb-2 email-to"
                name="to_address"
                ref={register({ required: true })}
              />
            </div>
            <br />
            {errors.to_address && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* Subject */}
        <div className="form-group row pt-0 mt-0 mb-0 pb-0">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6 pb-0 mb-0">
            <div className="d-flex">
              <label style={{ paddingTop: "8px", minWidth: "80px" }}>
                Subject
              </label>
              <span style={{ paddingTop: "8px" }}>:</span>
              <input
                type="text"
                className="form-control email-to"
                name="title"
                ref={register({ required: true })}
              />
            </div>
            <br />
            {errors.title && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* body */}
        <div className="form-group row mr-2 mt-0 pt-0">
          <div className="col">
            <textarea
              className="form-control"
              name="body"
              ref={register({ required: true })}
              rows="10"
              cols="100"
            ></textarea>
            <br />
            {errors.body && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* view upload file name */}
        <div className="mb-3">
          {attachment ? (
            <>
              <p>
                <span style={{ minWidth: "200px", paddingRight: "20px" }}>
                  File
                </span>
                :&nbsp;
                <span
                  className="link_style"
                  onClick={() => downloadFile(fileLink)}
                >
                  {attachment}
                </span>
              </p>
            </>
          ) : null}
        </div>
        {/* view upload image name */}
        <div className="mb-3">
          {image ? (
            <>
              <p>
                <span style={{ minWidth: "200px", paddingRight: "3px" }}>
                  Image
                </span>
                :&nbsp;
                <span
                  className="link_style"
                  onClick={() => downloadFile(imageLink)}
                >
                  {image}
                </span>
              </p>
            </>
          ) : null}
        </div>
        <div className="row d-flex ml-0">
          <button type="submit" className="btn btn-update">
            Send
          </button>

          {/* ------------------File Attachment--------------------- */}
          <input
            type="file"
            name="file_attachment"
            id="file_attachment"
            onChange={onchangeFileHandler}
            ref={register()}
          />
          <label className="" htmlFor="file_attachment">
            <i
              className="fas fa-paperclip pr-2 pl-3"
              title="Attach File"
              style={{
                fontSize: "20px",
                paddingTop: "10px",
                cursor: "pointer",
              }}
            ></i>
          </label>
          {/* ------------------------------------------ */}

          {/* ------------------Image Attachment--------------------- */}
          <input
            type="file"
            accept="image/*"
            name="image_attachment"
            id="image_attachment"
            onChange={onchangeImageHandler}
            ref={register()}
          />
          <label className="" htmlFor="image_attachment">
            <i
              className="far fa-file-image pr-2 pl-3"
              title="Attach Image"
              style={{
                fontSize: "20px",
                paddingTop: "10px",
                cursor: "pointer",
              }}
            ></i>
          </label>
          {/* ------------------------------------------ */}

          <i
            className="far fa-file pr-2 pl-3"
            title="Save as Draft"
            style={{
              fontSize: "20px",
              paddingTop: "10px",
              cursor: "pointer",
            }}
            onClick={handleSubmit((data) => saveAsDraft(data))}
          ></i>
          <i
            className="far fa-trash-alt pr-2 pl-3"
            title="Delete"
            onClick={() => {
              history.go(0);
            }}
            style={{
              fontSize: "20px",
              paddingTop: "10px",
              cursor: "pointer",
            }}
          ></i>
        </div>
      </form>

      {/* create form general info end */}

      <LoadingIndicator />
    </div>
  );
}
