import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import Moment from "moment";
export default function DatatablePkgHistoryList({ data, currentPage }) {
  const history = useHistory();
  function shortStringLength(str, length) {
    if (str.length > length) {
      return str.substring(0, length).concat(" ...");
    }
    return str;
  }

  //************************CHECK PERMISSION START****************************** */
  let isHideCreateButton = true;
  let isHideEditButton = true;
  let isHideViewButton = true;
  let isHideDeleteButton = true;
  if (localStorage.getItem("role_details") === null) {
    isHideCreateButton = false;
    isHideEditButton = false;
    isHideViewButton = false;
  } else {
    console.log(
      JSON.parse(localStorage.getItem("role_details")).package.charAt(0)
    );
    if (
      JSON.parse(localStorage.getItem("role_details")).package.charAt(0) === "0"
    ) {
      isHideCreateButton = true;
    } else {
      isHideCreateButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).package.charAt(1) === "0"
    ) {
      isHideViewButton = true;
    } else {
      isHideViewButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).package.charAt(2) === "0"
    ) {
      isHideEditButton = true;
    } else {
      isHideEditButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).package.charAt(3) === "0"
    ) {
      isHideDeleteButton = true;
    } else {
      isHideDeleteButton = false;
    }
  }
  //**********************CHECK PERMISSION END******************************** */
  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center td_slno_size">SL No.</th>
            <th className="inst-list-th text-center">Package Name</th>
            <th className="inst-list-th text-center">Description</th>
            <th className="inst-list-th text-center">Display</th>
            <th className="inst-list-th text-center">Created Date</th>
            <th className="inst-list-th text-center">Updated Date</th>
            {isHideViewButton ? null : (
              <th className="inst-list-th text-center">Action</th>
            )}
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">
                {(currentPage - 1) * 20 + (index + 1)}
              </td>
              <td className="inst-list-td text-left"> {row["title"]}</td>
              <td className="inst-list-td text-justify">
                {shortStringLength(row["description"], 70)}
              </td>
              <td className="inst-list-td"> {row["display"]}</td>
              <td className="inst-list-td">{row["created_at"]}</td>
              <td className="inst-list-td">{row["updated_at"]}</td>
              {isHideViewButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="View Details"
                  onClick={() =>
                    history.push({
                      pathname: "/package-history-detail",
                      state: { id: row["id"] },
                    })
                  }
                >
                  <i className="far fa-eye action-icon"></i>
                </td>
              )}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
