import React, { useState, useEffect, useMemo } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import PaginationComponent from "../Pagination/PaginationComponent";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableUserList from "./DatatableUserList";
import { toast } from "react-toastify";
export default function UserListView() {
  const [data, setData] = useState([]);
  const history = useHistory();
  const [updateApi, setUpdateApi] = useState(false);

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetAllUserNormalList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userListApiResp = await trackPromise(axios(config));
      // console.log(
      //   "userListApiResp - " + JSON.stringify(userListApiResp.data.msg)
      // );
      setData(userListApiResp.data.msg.reverse());
      setUpdateApi(false);
    }
    fetchData();
  }, [updateApi]);
  //GET ALL PACKAGE NAME -END

  /*TOGGLE USER STATUS START*/
  function toggleUserStatus(email, status) {
    //console.log("email: " + email + "Status: " + status);
    postData(email, status);
    async function postData(email, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/NormalUserStatusUpdate",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          email: email,
          status: status === "1" ? "0" : "1",
        },
      };
      const normalUserStatusResp = await trackPromise(axios(config));
      //console.log("normalUserStatusResp - " + normalUserStatusResp.data);
      if (normalUserStatusResp.data.status === "ok") {
        setUpdateApi(true);
        toast(`Status ${status === "1" ? "Disable" : "Enable"} Successfully`, {
          className: "toast-error",
        });
        setUpdateApi(true);
      } else {
        toast(normalUserStatusResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }
  /*TOGGLE USER STATUS END*/

  //************************CHECK PERMISSION START****************************** */
  let isHideCreateButton = true;
  let isHideEditButton = true;
  let isHideViewButton = true;
  if (localStorage.getItem("role_details") === null) {
    isHideCreateButton = false;
    isHideEditButton = false;
    isHideViewButton = false;
  } else {
    console.log(
      JSON.parse(localStorage.getItem("role_details")).user.charAt(0)
    );
    if (
      JSON.parse(localStorage.getItem("role_details")).user.charAt(0) === "0"
    ) {
      isHideCreateButton = true;
    } else {
      isHideCreateButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).user.charAt(1) === "0"
    ) {
      isHideViewButton = true;
    } else {
      isHideViewButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).user.charAt(2) === "0"
    ) {
      isHideEditButton = true;
    } else {
      isHideEditButton = false;
    }
  }
  //**********************CHECK PERMISSION END******************************** */
  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          (d.fname + " " + d.lname)
            .toLowerCase()
            .includes(search.toLowerCase()) ||
          d.email.toLowerCase().includes(search.toLowerCase()) ||
          d.role.toLowerCase().includes(search.toLowerCase()) ||
          d.mobile.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/
  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 className="heading">User Information</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <div className="d-flex float-left mb-3">
                            {isHideCreateButton ? null : (
                              <Link
                                to="/create-user"
                                className="border ml-1 add-button shadow"
                                title="Create User"
                              >
                                <i className="fas fa-plus add-button-icon"></i>
                              </Link>
                            )}
                          </div>
                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={search}
                              onChange={(e) => {
                                setSearch(e.target.value);
                                setCurrentPage(1);
                              }}
                            />
                            <div
                              className="border ml-1 inst-list-search-div"
                              title="Search"
                            >
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          {/* <DatatableUserList
                            data={currentData}
                            currentPage={currentPage}
                            isHideViewButton={isHideViewButton}
                            isHideEditButton={isHideEditButton}
                          /> */}
                          {/* table start */}
                          <div className="table-responsive">
                            <table className="table table-hover institute-table">
                              <thead>
                                <tr className="inst-list-heading">
                                  <th className="inst-list-th text-center td_slno_size">
                                    SL No.
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Name
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Email
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Mobile No.
                                  </th>
                                  <th className="inst-list-th text-center">
                                    User Type
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Status
                                  </th>
                                  {(() => {
                                    if (
                                      isHideViewButton === false &&
                                      isHideEditButton === false
                                    ) {
                                      return (
                                        <th
                                          className="inst-list-th text-center"
                                          colSpan="3"
                                        >
                                          Action
                                        </th>
                                      );
                                    } else if (isHideEditButton === false) {
                                      return (
                                        <th
                                          className="inst-list-th text-center"
                                          colSpan="2"
                                        >
                                          Action
                                        </th>
                                      );
                                    } else if (isHideEditButton === true) {
                                      return (
                                        <th className="inst-list-th text-center">
                                          Action
                                        </th>
                                      );
                                    } else {
                                      return null;
                                    }
                                  })()}
                                </tr>
                              </thead>
                              <tbody>
                                {currentData.map((row, index) => (
                                  <tr key={index}>
                                    <td className="inst-list-td text-center">
                                      {(currentPage - 1) * 20 + (index + 1)}
                                    </td>
                                    <td className="inst-list-td text-left td_name_size">
                                      {row["fname"]} {row["lname"]}
                                    </td>
                                    <td className="inst-list-td text-left td_email_size">
                                      {row["email"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["mobile"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["role"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["status"] === "1"
                                        ? "Enable"
                                        : "Disable"}
                                    </td>
                                    {isHideViewButton ? null : (
                                      <td
                                        className="inst-list-td text-center action td_action_size"
                                        title="View Details"
                                        onClick={() =>
                                          history.push({
                                            pathname: "/user-detail-view",
                                            state: { email: row["email"] },
                                          })
                                        }
                                      >
                                        <i className="far fa-eye action-icon"></i>
                                      </td>
                                    )}
                                    {isHideEditButton ? null : (
                                      <>
                                        <td
                                          className="inst-list-td text-center action td_action_size"
                                          title="Edit"
                                          onClick={() =>
                                            history.push({
                                              pathname: "/update-user",
                                              state: { email: row["email"] },
                                            })
                                          }
                                        >
                                          <i className="far fa-edit action-icon"></i>
                                        </td>
                                        <td
                                          className="inst-list-td text-center action td_action_size"
                                          title="Status"
                                        >
                                          <i
                                            onClick={() =>
                                              toggleUserStatus(
                                                row["email"],
                                                row["status"]
                                              )
                                            }
                                            className={`fas ${
                                              row["status"] === "1"
                                                ? "fa-toggle-on"
                                                : "fa-toggle-off"
                                            } action-icon`}
                                            style={{ fontSize: "20px" }}
                                          ></i>
                                        </td>
                                      </>
                                    )}
                                  </tr>
                                ))}
                              </tbody>
                            </table>
                          </div>
                          {/* table end */}
                          <PaginationComponent
                            total={totalItems}
                            itemsPerPage={ITEMS_PER_PAGE}
                            currentPage={currentPage}
                            onPageChange={(page) => setCurrentPage(page)}
                          />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
