import { Link, useLocation } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import Moment from "moment";
import ViewEmail from "./ViewEmail";
export default function Inbox() {
  const [data, setData] = useState([]);
  const [showViewEmail, setShowViewEmail] = useState(false);
  const [showInbox, setShowInbox] = useState(true);
  const [emailId, setEmailId] = useState("");
  const [updateApi, setUpdateApi] = useState(false);

  const history = useHistory();

  const emailAddress = localStorage.getItem("emailAddress");

  // FETCH GetEmailForInbox
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/GetEmailForInbox",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email: emailAddress,
          // email: "turzoxpress3@gmail.com",
        },
      };

      const response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.data));
      setData(
        response.data.data.map((d) => {
          return {
            select: false,
            email_id: d.email_id,
            title: d.title,
            sender: d.sender,
            sending_date: d.sending_date,
            updated_at: d.updated_at,
          };
        })
      );

      setUpdateApi(false);
    }
    fetchData();
  }, [updateApi]);

  async function deleteEmail(email_id, sender) {
    let formData = new FormData();
    formData.append("email_id", email_id);
    formData.append("to_address", emailAddress);

    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    //console.log("email_id: " + email_id + " -- sender: " + sender);
    var config = {
      method: "post",
      url: "/TrashEmailByReceiver",
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
      data: formData,
    };
    const response = await trackPromise(axios(config));
    //console.log("response - " + response.data);
    if (response.data.status === "ok") {
      toast("Conversation moved to Trash", {
        className: "toast-error",
      });
      setUpdateApi(true);
    } else {
      toast(response.data.msg, {
        className: "toast-error",
      });
    }
  }

  function shortStringLength(str, length) {
    if (str.length > length) {
      return str.substring(0, length).concat(" ...");
    }
    return str;
  }

  const emailArray = [];
  function deleteEmailById() {
    let arrayids = [];
    data.forEach((d) => {
      if (d.select) {
        arrayids.push(d.email_id);
      }
    });
    console.log(arrayids);
    // console.log(isChecked + "----" + email_id);
    // if (isChecked) {
    //   emailArray.push(email_id);
    // } else {
    //   const index = emailArray.indexOf(email_id);
    //   if (index > -1) {
    //     emailArray.splice(index, 1);
    //   }
    // }
    // console.log(emailArray.toString());
  }

  const [isAllChecked, setIsAllChecked] = useState(false);
  const [isHideDeleteBtn, setIsHideDeleteBtn] = useState(false);
  function toggleCheckedAllEmail(isChecked) {
    console.log(isChecked);
    setIsAllChecked(isChecked);
  }

  function isCheckAllEmail() {
    let count = 0;
    data.forEach((d) => {
      if (d.select) {
        count = count + 1;
      }
    });
    if (data.length === count) {
      setIsAllChecked(true);
    } else {
      setIsAllChecked(false);
    }
  }
  return (
    <>
      <div className="row mb-4 float-right mr-2 mt-3">
        {showViewEmail ? (
          <span
            className="border ml-1 add-button shadow float-right"
            style={{ cursor: "pointer" }}
            title="Back to Inbox"
            onClick={() => {
              setShowViewEmail(false);
              setShowInbox(true);
            }}
          >
            <i className="fas fa-arrow-left add-button-icon"></i>
          </span>
        ) : null}
      </div>

      <div className={`table-responsive ${showViewEmail ? null : "mt-5 pt-5"}`}>
        {isHideDeleteBtn ? (
          <i
            className="far fa-trash-alt  action-icon"
            title="Delete All"
            onClick={() => {
              deleteEmailById();
            }}
          ></i>
        ) : null}
        {showInbox ? (
          <table className="table table-hover institute-table">
            <thead>
              <tr className="inst-list-heading">
                <th className="inst-list-th text-center">
                  <input
                    type="checkbox"
                    checked={isAllChecked}
                    onChange={(e) => {
                      let value = e.target.checked;
                      setData(
                        data.map((d) => {
                          d.select = value;
                          return d;
                        })
                      );
                      setIsAllChecked(value);
                      setIsHideDeleteBtn(!isAllChecked);
                    }}
                  />
                </th>
                <th className="inst-list-th text-center">From</th>
                <th className="inst-list-th text-center">To</th>
                <th className="inst-list-th text-center">Subject</th>
                <th className="inst-list-th text-center td_email_date_size">
                  Date
                </th>
                <th className="inst-list-th text-center" colSpan="2">
                  Action
                </th>
              </tr>
            </thead>
            <tbody>
              {data.map((row, index) => (
                <tr key={index}>
                  <td className="inst-list-td text-center">
                    <input
                      type="checkbox"
                      checked={row.select}
                      onChange={(e) => {
                        let value = e.target.checked;
                        setData(
                          data.map((sd) => {
                            if (sd.email_id === row.email_id) {
                              sd.select = value;
                            }
                            return sd;
                          })
                        );
                        isCheckAllEmail();
                        setIsHideDeleteBtn(row.select);
                      }}
                    />
                  </td>
                  <td className="inst-list-td text-left">{row["sender"]}</td>
                  <td className="inst-list-td text-left">{emailAddress}</td>
                  <td className="inst-list-td text-left">
                    {shortStringLength(row["title"], 30)}
                  </td>
                  <td className="inst-list-td text-left">
                    {row["sending_date"]}
                  </td>
                  <td
                    className="inst-list-td text-center action td_action_size"
                    title="View Details"
                    onClick={() => {
                      setShowViewEmail(true);
                      setShowInbox(false);
                      setEmailId(row["email_id"]);
                    }}
                  >
                    <i className="far fa-eye action-icon"></i>
                  </td>
                  <td
                    className="inst-list-td text-center action td_action_size"
                    title="Delete"
                    onClick={() => {
                      deleteEmail(row["email_id"], row["sender"]);
                    }}
                  >
                    <i className="far fa-trash-alt action-icon"></i>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : null}
        {showViewEmail ? <ViewEmail email_id={emailId} /> : null}
      </div>
    </>
  );
}
