import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";

export default function DatatablePaymentList({
  data,
  currentPage,
  isHideViewButton,
}) {
  const history = useHistory();
  function extractNameFromPath(file_path) {
    var index = file_path.indexOf("_");
    var result = file_path.substr(index + 1);
    return result;
  }
  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center td_slno_size">SL No.</th>
            <th className="inst-list-th text-center">Invoice Number</th>
            <th className="inst-list-th text-center">Institute ID</th>
            <th className="inst-list-th text-center">Institute Name</th>
            <th className="inst-list-th text-center">Payment Document</th>
            <th className="inst-list-th text-center">Billing Month</th>
            <th className="inst-list-th text-center">Payment Date</th>
            <th className="inst-list-th text-center td_status_size">Status</th>
            {isHideViewButton ? null : (
              <th className="inst-list-th text-center">Action</th>
            )}
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">
                {(currentPage - 1) * 20 + (index + 1)}
              </td>
              <td className="inst-list-td text-left td_invoicenum_size">
                {row["id"]}
              </td>
              <td className="inst-list-td text-left td_isntid_size">
                {row["institute_id"]}
              </td>
              <td className="inst-list-td text-left td_isntname_size">
                {row["institute_name"]}
              </td>
              <td className="inst-list-td text-left td_isntname_size">
                {extractNameFromPath(row["file_path"])}
              </td>
              <td className="inst-list-td text-center td_billingmonth_size">
                {row["month"]}-{row["year"]}
              </td>
              <td className="inst-list-td text-center">{row["updated_at"]}</td>
              <td className="inst-list-td text-center">{row["status"]}</td>
              {isHideViewButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="View Details"
                  onClick={() =>
                    history.push({
                      pathname: "/payment-approve",
                      state: { id: row["id"] },
                    })
                  }
                >
                  <i className="far fa-eye action-icon"></i>
                </td>
              )}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
