import React, { useState, useEffect, useMemo } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableInstList from "./DatatableInstList";
import PaginationComponent from "../Pagination/PaginationComponent";
import { toast } from "react-toastify";
import Moment from "moment";
export default function InstituteListView() {
  const [data, setData] = useState([]);
  const [updateApi, setUpdateApi] = useState(false);
  const history = useHistory();
  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/institutes",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const allPkgApiResp = await trackPromise(axios(config));
      // console.log("allPkgApiResp - " + allPkgApiResp.data.msg.data);
      setData(allPkgApiResp.data.msg.data.reverse());
      setUpdateApi(false);
    }
    fetchData();
  }, [updateApi]);
  //GET ALL PACKAGE NAME -END

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.institute_id.toLowerCase().includes(search.toLowerCase()) ||
          d.name.toLowerCase().includes(search.toLowerCase()) ||
          d.email.toLowerCase().includes(search.toLowerCase()) ||
          d.phone.toLowerCase().includes(search.toLowerCase()) ||
          d.package_name.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/

  /*TOGGLE INSTITUTE STATUS START*/
  function toggleInstituteStatus(id, status, integer_id, institute_id) {
    //console.log("ID: " + id + "Status: " + status);

    postData(id, status);
    async function postData(id, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-status-update",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          id: id,
          active: status === "1" ? "0" : "1",
        },
      };
      const instStatusApiResp = await trackPromise(axios(config));
      //console.log("instStatusApiResp - " + instStatusApiResp.data);
      // if (instStatusApiResp.data.status === "ok") {
      //   toast(`Status ${status === "1" ? "Disable" : "Enable"} Successfully`, {
      //     className: "toast-error",
      //   });

      //   //setTimeout("history.go(0)", 2000);
      // } else {
      //   toast(instStatusApiResp.data.msg, {
      //     className: "toast-error",
      //   });
      // }
    }

    // post data to admin module start
    postDataAdmin(id, status, integer_id, institute_id);
    async function postDataAdmin(id, status, integer_id, institute_id) {
      let userStatus = status === "1" ? false : true;
      const data = {
        username: institute_id,
        instituteId: integer_id,
        userStatus: userStatus,
      };
      //console.log("sendApiResponse- " + JSON.stringify(data));
      const sendApiResponse = await trackPromise(
        axios.post("http://34.66.76.39:9091/api/other/update-by-status", data)
      );

      //console.log("sendApiResponse- " + JSON.stringify(sendApiResponse));
      if (sendApiResponse.data.status === "ok") {
        setUpdateApi(true);
        toast(
          `Status ${status === "1" ? "Disabled" : "Enabled"} Successfully!`,
          {
            className: "toast-error",
          }
        );
      } else {
        toast(sendApiResponse.data.msg, {
          className: "toast-error",
        });
      }
    }

    // post data to admin module end
  }
  /*TOGGLE INSTITUTE STATUS END*/

  //************************CHECK PERMISSION START****************************** */
  let isHideCreateButton = true;
  let isHideEditButton = true;
  let isHideViewButton = true;
  if (localStorage.getItem("role_details") === null) {
    isHideCreateButton = false;
    isHideEditButton = false;
    isHideViewButton = false;
  } else {
    console.log(
      JSON.parse(localStorage.getItem("role_details")).institute.charAt(0)
    );
    if (
      JSON.parse(localStorage.getItem("role_details")).institute.charAt(0) ===
      "0"
    ) {
      isHideCreateButton = true;
    } else {
      isHideCreateButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).institute.charAt(1) ===
      "0"
    ) {
      isHideViewButton = true;
    } else {
      isHideViewButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).institute.charAt(2) ===
      "0"
    ) {
      isHideEditButton = true;
    } else {
      isHideEditButton = false;
    }
  }
  //**********************CHECK PERMISSION END******************************** */
  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 className="heading">Institute Information</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <div className="d-flex float-left mb-3">
                            {isHideCreateButton ? null : (
                              <Link
                                to="/institute-create"
                                className="border ml-1 add-button shadow"
                                title="Create Institute"
                              >
                                <i className="fas fa-plus add-button-icon"></i>
                              </Link>
                            )}
                          </div>
                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={search}
                              onChange={(e) => {
                                setSearch(e.target.value);
                                setCurrentPage(1);
                              }}
                            />
                            <div
                              className="border ml-1 inst-list-search-div"
                              title="Search"
                            >
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          {/* <DatatableInstList
                            data={currentData}
                            currentPage={currentPage}
                          /> */}
                          {/* table start */}
                          <div className="table-responsive">
                            <table className="table table-hover institute-table">
                              <thead>
                                <tr className="inst-list-heading">
                                  <th className="inst-list-th text-center td_slno_size">
                                    SL No.
                                  </th>
                                  <th className="inst-list-th text-center td_isntid_size">
                                    Institute ID
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Institute Name
                                  </th>
                                  <th className="inst-list-th text-center td_inst_email_size">
                                    Email
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Phone / Mobile No.
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Package Name
                                  </th>
                                  <th className="inst-list-th text-center">
                                    Subscription End Date
                                  </th>
                                  <th className="inst-list-th text-center td_status_size">
                                    Status
                                  </th>
                                  {(() => {
                                    if (
                                      isHideViewButton === false &&
                                      isHideEditButton === false
                                    ) {
                                      return (
                                        <th
                                          className="inst-list-th text-center"
                                          colSpan="3"
                                        >
                                          Action
                                        </th>
                                      );
                                    } else if (isHideEditButton === false) {
                                      return (
                                        <th
                                          className="inst-list-th text-center"
                                          colSpan="2"
                                        >
                                          Action
                                        </th>
                                      );
                                    } else if (isHideEditButton === true) {
                                      return (
                                        <th className="inst-list-th text-center">
                                          Action
                                        </th>
                                      );
                                    } else {
                                      return null;
                                    }
                                  })()}
                                </tr>
                              </thead>
                              <tbody>
                                {currentData.map((row, index) => (
                                  <tr key={index}>
                                    <td className="inst-list-td text-center">
                                      {(currentPage - 1) * 20 + (index + 1)}
                                    </td>
                                    <td className="inst-list-td text-left">
                                      {row["institute_id"]}
                                    </td>
                                    <td className="inst-list-td text-left">
                                      {row["name"]}
                                    </td>
                                    <td className="inst-list-td text-left">
                                      {row["email"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["phone"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["package_name"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {Moment.utc(
                                        row["subscription_e_date"]
                                      ).format("DD-MM-YYYY")}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["active"] === "1"
                                        ? "Enable"
                                        : "Disable"}
                                    </td>
                                    {isHideViewButton ? null : (
                                      <td
                                        className="inst-list-td text-center action td_action_size"
                                        title="View Details"
                                        onClick={() =>
                                          history.push({
                                            pathname: "/institute-detail",
                                            state: { id: row["id"] },
                                          })
                                        }
                                      >
                                        <i className="far fa-eye action-icon"></i>
                                      </td>
                                    )}
                                    {isHideEditButton ? null : (
                                      <>
                                        <td
                                          className="inst-list-td text-center action td_action_size"
                                          title="Edit"
                                          onClick={() =>
                                            history.push({
                                              pathname: "/institute-update",
                                              state: { id: row["id"] },
                                            })
                                          }
                                        >
                                          <i className="far fa-edit action-icon"></i>
                                        </td>
                                        <td
                                          className="inst-list-td text-center action td_action_size"
                                          title="Status"
                                        >
                                          <i
                                            onClick={() =>
                                              toggleInstituteStatus(
                                                row["id"],
                                                row["active"],
                                                row["integer_id"],
                                                row["institute_id"]
                                              )
                                            }
                                            className={`fas ${
                                              row["active"] === "1"
                                                ? "fa-toggle-on"
                                                : "fa-toggle-off"
                                            } action-icon`}
                                            style={{ fontSize: "20px" }}
                                          ></i>
                                        </td>
                                      </>
                                    )}
                                  </tr>
                                ))}
                              </tbody>
                            </table>
                          </div>
                          {/* table end */}

                          <PaginationComponent
                            total={totalItems}
                            itemsPerPage={ITEMS_PER_PAGE}
                            currentPage={currentPage}
                            onPageChange={(page) => setCurrentPage(page)}
                          />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
