import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Compose from "./Compose";
import Inbox from "./Inbox";
import Sent from "./Sent";
import Draft from "./Draft";
import Trash from "./Trash";
import { toast } from "react-toastify";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";

export default function Email() {
  const [showCompose, setShowCompose] = useState(false);
  const [showInbox, setShowInbox] = useState(true);
  const [showSent, setShowSent] = useState(false);
  const [showDraft, setShowDraft] = useState(false);
  const [showTrash, setShowTrash] = useState(false);

  const [totalEmail, setTotalEmail] = useState(false);

  // FETCH totalEmail
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      const emailAddress = localStorage.getItem("emailAddress");
      var config = {
        method: "post",
        url: "/GetTotalEmailsCounter",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email: emailAddress,
        },
      };

      const response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.data));
      if (response.data.status === "ok") {
        setTotalEmail(response.data.data);
      } else {
        toast(response.data.msg, {
          className: "toast-error",
        });
      }
    }
    fetchData();
  }, []);

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  {/* Profile Edit start */}
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Email</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-lg-2 col-md-3 col-sm-6">
                          <div
                            className="nav flex-column nav-tabs h-100"
                            id="vert-tabs-tab"
                            role="tablist"
                            aria-orientation="vertical"
                            style={{
                              backgroundColor: "#fff",
                              paddingTop: "40px",
                            }}
                          >
                            <span
                              className="shadow badge badge-pill compose__btn"
                              onClick={() => {
                                setShowCompose(true);
                                setShowInbox(false);
                                setShowSent(false);
                                setShowDraft(false);
                                setShowTrash(false);
                              }}
                            >
                              <i className="fas fa-plus compose__icon"></i>
                              Compose
                            </span>
                            <span
                              style={{
                                borderBottom: "1px solid #B9B9B9",
                                margin: "24px 10px 8px 10px",
                              }}
                            ></span>
                            <a
                              className={`nav-link edit-profile cursor-pointer ${
                                showInbox ? "active" : null
                              }`}
                              style={{ color: "#656565" }}
                              onClick={() => {
                                setShowCompose(false);
                                setShowInbox(true);
                                setShowSent(false);
                                setShowDraft(false);
                                setShowTrash(false);
                              }}
                            >
                              <i className="fas fa-inbox pr-2 pl-3"></i>
                              Inbox{" "}
                              <span>
                                ({totalEmail.inbox ? totalEmail.inbox : 0})
                              </span>
                            </a>
                            <a
                              className={`nav-link edit-profile cursor-pointer ${
                                showSent ? "active" : null
                              }`}
                              style={{ color: "#656565" }}
                              onClick={() => {
                                setShowCompose(false);
                                setShowInbox(false);
                                setShowSent(true);
                                setShowDraft(false);
                                setShowTrash(false);
                              }}
                            >
                              <i className="fas fa-share pr-2 pl-3"></i>Sent{" "}
                              <span>
                                ({totalEmail.sentbox ? totalEmail.sentbox : 0})
                              </span>
                            </a>
                            <a
                              className={`nav-link edit-profile cursor-pointer ${
                                showDraft ? "active" : null
                              }`}
                              style={{ color: "#656565" }}
                              onClick={() => {
                                setShowCompose(false);
                                setShowInbox(false);
                                setShowSent(false);
                                setShowDraft(true);
                                setShowTrash(false);
                              }}
                            >
                              <i className="far fa-file pr-2 pl-3"></i>Draft{" "}
                              <span>
                                ({totalEmail.draft ? totalEmail.draft : 0})
                              </span>
                            </a>
                            <a
                              className={`nav-link edit-profile cursor-pointer ${
                                showTrash ? "active" : null
                              }`}
                              style={{ color: "#656565" }}
                              onClick={() => {
                                setShowCompose(false);
                                setShowInbox(false);
                                setShowSent(false);
                                setShowDraft(false);
                                setShowTrash(true);
                              }}
                            >
                              <i className="far fa-trash-alt pr-2 pl-3"></i>
                              Trash{" "}
                              <span>
                                ({totalEmail.deleted ? totalEmail.deleted : 0})
                              </span>
                            </a>
                          </div>
                        </div>
                        <div
                          className="col-lg-10 col-md-9 col-sm-6"
                          style={{ padding: "1.25rem" }}
                        >
                          <div
                            className="tab-content"
                            id="vert-tabs-tabContent"
                          >
                            {showCompose ? <Compose /> : null}
                            {showInbox ? <Inbox /> : null}
                            {showSent ? <Sent /> : null}
                            {showDraft ? <Draft /> : null}
                            {showTrash ? <Trash /> : null}
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
