import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { toast } from "react-toastify";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

export default function Datatable({
  data,
  currentPage,
  isHideEditButton,
  isHideViewButton,
  isHideDeleteButton,
}) {
  const history = useHistory();
  const [isDelete, setIsDelete] = useState(false);

  async function deletePackage(packageId, packageType) {
    if (packageType === "custom") {
      toast("Package already used!", {
        className: "toast-error",
      });
    } else {
      // console.log("package id: " + packageId);
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: `/package-delete`,
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          package_id: packageId,
        },
      };
      const deletePkgApiResp = await trackPromise(axios(config));
      //console.log("deletePkgApiResp - " + deletePkgApiResp.data);
      if (deletePkgApiResp.data.status === "ok") {
        toast("Package Deleted Successfully", {
          className: "toast-error",
        });
        history.go(0);
      } else {
        toast(deletePkgApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }

  // ----------------------------------------

  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {};

  const handleClose = () => {
    setOpen(false);
  };

  const handleYes = () => {
    setOpen(false);
    setIsDelete(true);
  };

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center td_slno_size">SL No.</th>
            <th className="inst-list-th text-center td_pkg_title_size2">
              Package Name
            </th>
            <th className="inst-list-th text-center">Package Type</th>
            <th className="inst-list-th text-center">Display</th>
            <th className="inst-list-th text-center">Total Amount</th>
            {(() => {
              if (
                isHideViewButton === false &&
                isHideEditButton === false &&
                isHideDeleteButton === false
              ) {
                return (
                  <th className="inst-list-th text-center" colSpan="3">
                    Action
                  </th>
                );
              } else if (
                (isHideViewButton === false &&
                  isHideEditButton === false &&
                  isHideDeleteButton === true) ||
                (isHideViewButton === false &&
                  isHideEditButton === true &&
                  isHideDeleteButton === false) ||
                (isHideViewButton === true &&
                  isHideEditButton === false &&
                  isHideDeleteButton === false)
              ) {
                return (
                  <th className="inst-list-th text-center" colSpan="2">
                    Action
                  </th>
                );
              } else if (
                (isHideViewButton === true &&
                  isHideEditButton === true &&
                  isHideDeleteButton === false) ||
                (isHideViewButton === true &&
                  isHideEditButton === false &&
                  isHideDeleteButton === true) ||
                (isHideViewButton === false &&
                  isHideEditButton === true &&
                  isHideDeleteButton === true)
              ) {
                return (
                  <th className="inst-list-th text-center" colSpan="1">
                    Action
                  </th>
                );
              } else {
                return null;
              }
            })()}
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">
                {(currentPage - 1) * 20 + (index + 1)}
              </td>
              <td className="inst-list-td text-left"> {row["title"]}</td>
              <td className="inst-list-td text-center">
                {row["type"].charAt(0).toUpperCase() + row["type"].slice(1)}
              </td>
              <td className="inst-list-td text-center"> {row["display"]}</td>
              <td className="inst-list-td text-center">
                {row["total_amount"]}
              </td>
              {isHideViewButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="View Details"
                  onClick={() =>
                    history.push({
                      pathname: "/package-details",
                      state: { id: row["id"] },
                    })
                  }
                >
                  <i className="far fa-eye action-icon"></i>
                </td>
              )}
              {isHideEditButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="Edit"
                  onClick={() =>
                    history.push({
                      pathname: "/package-edit",
                      state: { id: row["id"] },
                    })
                  }
                >
                  <i className="far fa-edit action-icon"></i>
                </td>
              )}
              {isHideDeleteButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="Delete"
                  onClick={() => {
                    window.confirm("Are You Sure?") &&
                      deletePackage(row["id"], row["type"]);
                  }}
                >
                  <i className="far fa-trash-alt action-icon"></i>
                </td>
              )}
            </tr>
          ))}
        </tbody>
      </table>
      <Dialog
        open={open}
        onClose={handleClose}
        //onConfirm={}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Are You Sure?"}</DialogTitle>

        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            className="alertNO"
          >
            No
          </Button>
          <Button
            onClick={() => {
              setOpen(false);
              //onConfirm();
            }}
            className="alertYes"
          >
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
