import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { toast } from "react-toastify";
import Moment from "moment";
export default function DatatableBillingList({
  data,
  currentPage,
  isHideEditButton,
  isHideViewButton,
}) {
  const history = useHistory();

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center td_slno_size">SL No.</th>
            <th className="inst-list-th text-center">Invoice Number</th>
            <th className="inst-list-th text-center">Institute ID</th>
            <th className="inst-list-th text-center">Institute Name</th>
            <th className="inst-list-th text-center">Billing Month</th>
            <th className="inst-list-th text-center">Billing Amount</th>
            <th className="inst-list-th text-center">Bill Generate Date</th>

            {(() => {
              if (isHideViewButton === false && isHideEditButton === false) {
                return (
                  <th className="inst-list-th text-center" colSpan="2">
                    Action
                  </th>
                );
              } else if (
                isHideViewButton === false ||
                isHideEditButton === false
              ) {
                return <th className="inst-list-th text-center">Action</th>;
              } else {
                return null;
              }
            })()}
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">
                {(currentPage - 1) * 20 + (index + 1)}
              </td>
              <td className="inst-list-td text-left td_invoicenum_size">
                {row["id"]}
              </td>
              <td className="inst-list-td text-left td_isntid_size">
                {row["institute_id"]}
              </td>
              <td className="inst-list-td text-left td_isntname_size">
                {row["institute_name"]}
              </td>
              <td className="inst-list-td text-center td_billingmonth_size">
                {row["month"]}-{row["year"]}
              </td>
              <td className="inst-list-td text-center">{row["amount"]}</td>
              <td className="inst-list-td text-center">{row["created_at"]}</td>
              {isHideViewButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="View Details"
                  onClick={() =>
                    history.push({
                      pathname: "/billing-invoice-view",
                      state: { id: row["id"] },
                    })
                  }
                >
                  <i className="far fa-eye action-icon"></i>
                </td>
              )}
              {isHideEditButton ? null : (
                <td
                  className="inst-list-td text-center action td_action_size"
                  title="Edit"
                  onClick={() =>
                    history.push({
                      pathname: "/update-bill",
                      state: { id: row["id"] },
                    })
                  }
                >
                  <i className="far fa-edit action-icon"></i>
                </td>
              )}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
