import React, { useState, useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";

import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatablePaymentList from "./DatatablePaymentList";
export default function PaymentListView() {
  const [data, setData] = useState([]);

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetAllInstituteListWithInvoice",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const paymentApiResp = await trackPromise(axios(config));
      // console.log(
      //   "paymentApiResp - " + JSON.stringify(paymentApiResp.data.msg.data)
      // );

      let response = paymentApiResp.data.msg.data.reverse();
      /*BILLING STATUS FILTER BY INVOICE STATUS == PENDING  START*/
      let jsObjects = response;
      let result = jsObjects.filter((obj) => {
        return obj.file_path !== "";
      });
      //console.log(result);
      /*BILLING STATUS FILTER BY INVOICE STATUS == PENDING  END*/

      setData(result);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_name.toLowerCase().includes(search.toLowerCase()) ||
          d.status.toLowerCase().includes(search.toLowerCase()) ||
          d.updated_at.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/

  //************************CHECK PERMISSION START****************************** */
  let isHideCreateButton = true;
  let isHideEditButton = true;
  let isHideViewButton = true;
  if (localStorage.getItem("role_details") === null) {
    isHideCreateButton = false;
    isHideEditButton = false;
    isHideViewButton = false;
  } else {
    console.log(
      JSON.parse(localStorage.getItem("role_details")).payment.charAt(0)
    );
    if (
      JSON.parse(localStorage.getItem("role_details")).payment.charAt(0) === "0"
    ) {
      isHideCreateButton = true;
    } else {
      isHideCreateButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).payment.charAt(1) === "0"
    ) {
      isHideViewButton = true;
    } else {
      isHideViewButton = false;
    }
    if (
      JSON.parse(localStorage.getItem("role_details")).payment.charAt(2) === "0"
    ) {
      isHideEditButton = true;
    } else {
      isHideEditButton = false;
    }
  }
  //**********************CHECK PERMISSION END******************************** */
  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 className="heading">Payment Information</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <div className="d-flex float-left mb-3">
                            {/* QA sohag vai told to hide paymenthistory*/}
                            {/* <Link
                              to="/payment-history-view"
                              className="border ml-1 add-button shadow"
                              title="Payment History"
                            >
                              <i className="add-button-icon fas fa-history"></i>
                            </Link> */}
                          </div>
                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={search}
                              onChange={(e) => {
                                setSearch(e.target.value);
                                setCurrentPage(1);
                              }}
                            />
                            <div
                              className="border ml-1 inst-list-search-div"
                              title="Search"
                            >
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          <DatatablePaymentList
                            data={currentData}
                            currentPage={currentPage}
                            isHideViewButton={isHideViewButton}
                          />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
